export function formatDateTime(dateString) {
  if (!dateString) return 'No date'

  const dateObj = new Date(dateString)

  const year = dateObj.getUTCFullYear()
  const month = String(dateObj.getUTCMonth() + 1).padStart(2, '0')
  const day = String(dateObj.getUTCDate()).padStart(2, '0')
  const hours = String(dateObj.getUTCHours()).padStart(2, '0')
  const minutes = String(dateObj.getUTCMinutes()).padStart(2, '0')

  return `${hours}:${minutes}-${day}/${month}/${year}`
}

export function convertLocalToUTC(localDate) {
  // Táº¡o má»™t Ä‘á»‘i tÆ°á»£ng Date tá»« ngÃ y giá» Ä‘á»‹a phÆ°Æ¡ng
  const date = new Date(localDate)

  // Láº¥y cÃ¡c thÃ nh pháº§n cá»§a ngÃ y giá» UTC
  const year = date.getUTCFullYear()
  const month = date.getUTCMonth() // LÆ°u Ã½: ThÃ¡ng trong JavaScript báº¯t Ä‘áº§u tá»« 0
  const day = date.getUTCDate()
  const hours = date.getUTCHours()
  const minutes = date.getUTCMinutes()
  const seconds = date.getUTCSeconds()

  // Táº¡o má»™t Ä‘á»‘i tÆ°á»£ng Date má»›i vá»›i cÃ¡c thÃ nh pháº§n UTC
  const utcDate = new Date(Date.UTC(year, month, day, hours, minutes, seconds))

  return utcDate
}

